import React, { useEffect, useState } from 'react'
import Filter from './Components/Filter'
import PersonForm from './Components/PersonForm'
import personService from "./services/phonebook"
import Person from "./Components/Person"
import Notification from "./Components/Notification"
import "./index.css"

const App = () => {
  const [persons, setPersons] = useState([])
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ newFilter, setnewFilter ] = useState('')
  const [ message, setMessage] = useState(null)
  const [ msgType, setMsgType] = useState('')

  useEffect(() => {
    personService
    .getAll()
    .then(initialPersons => {
      setPersons(initialPersons)
    })
  }, [] )

  const popUp = (msg, name, type) => {
    setNewName('')
    setNewNumber('')
    setMsgType(type)
    setMessage(`${msg} ${name}`)
    setTimeout(() => {
      setMessage(null)
    }, 5000)
  }
  
  const addPerson = (event) => {
    event.preventDefault()
    const personObject = {
      name: newName,
      number: newNumber
    }
    if (persons.every( person => person.name !== newName)) {
      personService
      .create(personObject)
      .then(returnedPerson => {
        setPersons(persons.concat(returnedPerson))
      })
      .catch(error => {
        console.log(error.response.data)
        popUp('', error.response.data, 'negative')
      })
      popUp('Added', personObject.name, 'positive')  

      }
    else { // Update Person
      if (window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`)) {
      const previousPerson = persons.find( n => n.name === personObject.name)
      personService
      .update(previousPerson.id, personObject)
      .then(returnedPerson => {
        setPersons(persons.map(person => person.id !== previousPerson.id ? person : returnedPerson))
      })
      .catch(error => {
        setMsgType("error")
        setMessage(`Information of ${personObject.name} has already been removed from server`)
      })
      popUp('Updated', personObject.name, 'positive')
    }
  }
  }
 
  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleDelete = (person) => {
    if (window.confirm(`Are you sure you want to delete ${person.name}?`)) {
      personService
      .del(person.id)
      .then(deletedPerson => {
        setPersons(persons.filter( person => person.name !== deletedPerson.name))
      })
      popUp('Removed', person.name, 'positive')
    }
  }

  //const entriesToShow = persons.filter( person => person.name.toLowerCase().includes(newFilter.toLowerCase()))

  const entriesToShow = newFilter.length === 0 ?
    persons : 
    persons.filter(p => p.name.toLowerCase().indexOf(newFilter.toLowerCase()) > 0 )

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={message} type={msgType} />
      <Filter value={newFilter} onChange={ event => setnewFilter(event.target.value)} />

      <h2>add a new</h2>
      <PersonForm f1={addPerson} n1={newName} n2={newNumber} h1={handleNameChange} h2={handleNumberChange} />

      <h2>Numbers</h2>
      <ul>
        {entriesToShow.map( person => 
        <Person key={person.name} person={person} handleDelete={() => handleDelete(person) }/>)}
        </ul>
    </div>
  )

}

export default App
